import _ from "lodash";
import React, { useEffect, useState } from "react";
import { useSelector } from "react-redux";
import { getVendorList as ShipmentVenderEffect } from "../../../redux/shipment/ShipmentEffects";
import { getVendorList as POVenderEffect } from "../../../redux/purchaseOrder/PurchaseOrderEffects";
import FormSelectBox from "../Inputs/FormSelectBox";
import i18n from 'i18next';

const VendorSelector = ({
  placeholder = i18n.t('translation:default.fields.vendorName.placeholder'),
  label = i18n.t('translation:default.fields.vendorName.label'),
  rules = [],
  name,
  singleLineMode,
  inverMode,
  editMode,
  colon,
  onChange = () => {},
  vendorType = "SHIPMENT",
  margin,
  disableValidation = false,
  mode = "",
  labelWithValue = false
}) => {
  const [options, setOptions] = useState([]);
  const [loading, setLoading] = useState(false);

  const EFFECTS = {
    SHIPMENT: ShipmentVenderEffect,
    PO: POVenderEffect,
  };

  const { selectedCompany } = useSelector((state) => state.auth);
  useEffect(() => {
    if (selectedCompany && selectedCompany.companyId)
      getStatusList(selectedCompany.companyId);
  }, [selectedCompany]);

  const getStatusList = async (Customerkey) => {
    try {
      setLoading(true);
      const { data } = await EFFECTS[vendorType]({ Customerkey });
      setOptions(data);
      setLoading(false);
    } catch (error) {
      console.log(error);
      setOptions([]);
      setLoading(false);
    }
  };

  const handleChange = (value) => {
    if(labelWithValue=== true){
      onChange(value);
    } else if (value) {
      onChange(_.find(options, { vendorKey: value?.value }));
    } else {
      onChange({});
    }
  };

  return (
    <FormSelectBox
      labelWithValue = {labelWithValue}
      loading={loading}
      options={options.map((row) => ({
        label: row.vendorName,
        value: row.vendorKey,
      }))}
      placeholder={placeholder}
      label={label}
      margin={margin}
      rules={rules}
      name={name}
      singleLineMode={singleLineMode}
      inverMode={inverMode}
      editMode={editMode}
      colon={colon}
      onChange={handleChange}
      disableValidation={disableValidation}
      mode={mode}
    />
  );
};

export default VendorSelector;
